@import "../theming";

@mixin inverted-mode($light-mode-value, $dark-mode-value, $is-body: false, $has-system-selector: false) {
  $default-mode-value: if($u-default-theme-dark, $dark-mode-value, $light-mode-value);
  $inverse-mode-value: if($u-default-theme-dark, $light-mode-value, $dark-mode-value);
  $inverse-mode-name: if($u-default-theme-dark, "light", "dark");

  @content($default-mode-value);

  @if not $is-body {
    @if $has-system-selector {
      %u-bg-light {
        @content($light-mode-value);
      }

      %u-bg-dark {
        @content($dark-mode-value);
      }
    } @else {
      @at-root %u-bg-light & {
        @content($light-mode-value);
      }

      @at-root %u-bg-dark & {
        @content($dark-mode-value);
      }
    }
  }

  @if $is-body {
    &.u-#{$inverse-mode-name}-mode {
      @content($inverse-mode-value);
    }
  } @else if $has-system-selector {
    $current-selector: &;
    @at-root body {
      &.u-#{$inverse-mode-name}-mode#{$current-selector} {
        @content($inverse-mode-value);
      }
    }
  } @else {
    @at-root body.u-#{$inverse-mode-name}-mode & {
      @content($inverse-mode-value);
    }
  }
}
